ul.lista{
  margin: 0px;
  padding: 0px;
  list-style-type: none;
  margin-bottom: 30px;
}

ul.lista li{
  height: 70px;
  border-bottom: 1px solid #E7E7E7;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}



ul.lista li .nombre{
  height: 60px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
}

ul.lista li .nombre.nombreUsuario span{
  font-family: 'Montserrat', sans-serif;
  color: #5A5A5A;
  font-size: 14px;
  font-weight: 400;
}

ul.todo-list li .nombre span.nombre.nombreUsuario{
  font-size: 11px;
  color: #A6A6A6;
}


ul.todo-list li .nombreUsuario{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 60px;
  min-width: 90px;
}
ul.todo-list li .nombreUsuario h3{
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  color: #9D3872;
  font-size: 24px;
}



